.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.button {
  height: 48px;
  width: 236px;
  max-width: 236px;
  color: var(--color-primary);
  border: var(--button-border);
  transition: color 250ms var(--timing-function);
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  line-height: 1.25px;
  display: flex;
}

.button--transporant {
  transition: background-color 250ms var(--timing-function);
  background-color: rgba(0, 0, 0, .4);
}

.button--black {
  background-color: var(--color-black);
  transition: background-color 250ms var(--timing-function);
}

.button:hover, .button:focus {
  background-color: var(--color-white);
}

.reservation-button {
  color: #fff;
  text-transform: uppercase;
  transition: background-color 250ms var(--timing-function);
  background-color: #000;
  border: 1px solid #f3c653;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  line-height: 1.21px;
  display: flex;
}

.reservation-button--section {
  width: 434px;
  height: 64px;
  border-radius: 25px;
}

.reservation-button--modal {
  width: 592px;
  height: 64px;
  border-radius: 2px;
}

@media screen and (max-width: 1024px) {
  .reservation-button {
    max-width: 384px;
    height: 64px;
    font-size: 20px;
    line-height: 1.2px;
  }
}

.reservation-button:hover, .reservation-button:focus {
  background-color: var(--color-primary);
  color: var(--color-black);
}

.section {
  margin-left: auto;
  margin-right: auto;
}

.container {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 414px) {
  .container {
    width: var(--mobile);
  }
}

@media screen and (min-width: 1024px) {
  .container {
    width: var(--tablet);
    padding-left: 47px;
    padding-right: 47px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: var(--desktop);
